<template>
  <div class="main-ctn">
    <el-row :gutter="20">
      <el-col :span="17">
        <div class="grid-content bg-purple">
          <div class="article-header">
            <div class="article-title">{{articleInfo.subjectName}}</div>
            <div class="article-info clearfix">
              <div class="article-info-left">
                <span>作者：{{articleInfo.staffname}}</span><span>|</span>
                <span>发布时间：{{articleInfo.createDate}}</span><span>|</span>
                <span>浏览{{readNum}}次</span><span>|</span>
                <span>评论{{commentNum}}次</span>
              </div>
              <div class="article-info-right">
                <span class="alterArticle" @click="collect()"><i class="el-icon-star-off"></i>&nbsp;收藏</span><span>|</span>
                <span class="delArticle" @click="recommend() "><i class="fa fa-thumbs-o-up"></i>&nbsp;推荐</span>
              </div>
            </div>
          </div>
          <div class="article-content">
            <div class="know-article">
            <div class="know-articles" v-html="'周报描述：'+'<br>'+'<p>'+this.articleinfo.weeklyDesc+'</p>'"></div>
            <div class="know-articles" v-html="'下周计划：'+'<br>'+'<p>'+this.articleinfo.nextWeeklyDesc+'</p>'"></div>
            <div class="know-articles" v-html="'学习笔记：'+'<br>'+'<p>'+this.articleinfo.subjectNote+'</p>'"></div>
            </div>
            <div class="article-attachs">
              <span class="tag-name">附件：</span>
              <span class="tag-content" v-show="typeof this.articleInfo.attaches === 'undefined'||articleInfo.attaches.length == 0">无</span>
              <span class="attach-group" v-for="(item,index) in articleInfo.attaches" @click="previewAttach(item)"> {{item.attachName}}</span>
            </div>
            <div class="article-tag">
              <span class="tag-name">标签：</span>
              <span class="tag-content">{{articleInfo.keyWord}}</span>
            </div>
          </div>
        </div>
      </el-col>
      <el-col :span="7">
        <div class="grid-content bg-purple">
          <div class="total-score">
            <div class="score-star">
              <span>好评率</span>
              <el-rate class="rating" v-model="totalRateValue" disabled show-score text-color="#ff9900" score-template="{value}"></el-rate>
              <div class="rating-num">已有{{commentNum}}人评分</div>
            </div>
          </div>
        </div>
      </el-col>
      <el-col :span="7">
        <div class="grid-content bg-purple">
          <div class="visit-title">访问记录</div>
          <div class="visit-content">
            <div class="visit-group" v-for="(item,index) in visitRoles">
              <div class="recently-visit">
                <span><i class="fa fa-street-view recently-visit-icon" aria-hidden="true"></i></span>
                <span class="visit-time">{{item.time}}</span>
                <span class="visit-user-name">{{item.staffUser.staffname}}</span>
              </div>
            </div>
            <div class="comment-part clearfix">
               <div class="visit-pege">      
                 <el-pagination small :current-page="currentPages" :page-sizes="[ 5,10 ]" :page-size="pageSizes" layout="total,prev, pager, next, jumper" :total="totals">
                 </el-pagination>
               </div>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="17">
        <div class="grid-content bg-purple">
          <div class="rate-comment">
            <div class="rating-part clearfix">
              <div class="user-rate clearfix">
                <span>评分：</span>
                <el-rate v-model="userRateValue"  show-score text-color="#ff9900" score-template="{value}">></el-rate>
              </div>
              <div class="user-comment">
                <el-input type="textarea" :rows="2" placeholder="在这里发表您的评论..." v-model="userCommentValue" resize="none">
                </el-input>
              </div>
              <div class="comment-btn">
                <el-button size="small" type="primary" :disabled="commentIsNotNull"  @click="addComment()">
                  发布评论
                </el-button>
              </div>
            </div>
            <div class="comment-part clearfix">
              <h4>评论</h4>
              <p v-if="commentsList.length==0">暂无评论，我来发表第一篇评论！</p>
               <div v-else>
                 <div class="comment clearfix" v-for="(item,index) in commentsList" v-bind:index="index" >
                   <b>{{item.staff.staffname}}&nbsp;&nbsp;<span>{{item.time}}</span></b>
                   <span @click="delOneComment(item)" class="delcomment">×</span>
                   <p class="pa-comment">{{item.comment}}</p>
                   <div class="icon-comment" @click="togglecomment(item,{})"><i class="fa fa-commenting-o" aria-hidden="true"></i></div><br>
                   <transition name="adc">
                   <div class="pa-comments clearfix" v-show="item.od==1">
                    <div class="pauser-comment">
                      <el-input type="textarea" placeholder="在这里发表您的回复..." :rows="2" v-model="userreplyValue" resize="none">
                      </el-input>
                    </div>
                    <div class="pacomment-btn clearfix">
                     <div @click="addreplypa(item)" class="reply-success"><i class="el-icon-success"></i>发布回复</div>
                     <div class="reply-error" @click="commentcancel(item,{})"><i class="el-icon-error"></i>取消发布</div>
                    </div>
                   </div>
                   </transition>
                   <div v-if="item.children.length > 0" class="cdn-comment">
                      <div class="reply" v-for="children in item.children.slice(1,[item.children.length])" >
                        <span>{{children.staff.staffname}}&nbsp;&nbsp;回复&nbsp;&nbsp;{{children.parentUser.staffname}}<span>&nbsp;&nbsp;{{children.time}}</span></span>
                        <div class="icon-comments"  @click="togglereply(children,children.od)"><i class="fa fa-commenting-o" aria-hidden="true"></i></div>
                        <p  class="cd-comment"  >{{children.comment}}</p>
                        <transition name="adc">
                        <div class="cd-comments clearfix" v-show="children.od==1">
                          <div class="pauser-comment">
                           <el-input type="textarea" placeholder="在这里发表您的回复..." :rows="2" v-model="userreplyValue" resize="none">
                           </el-input>
                          </div>
                          <div class="adcomment-btn clearfix">
                           <div @click="addreplycd(children)" class="reply-success"><i class="el-icon-success"></i>发布回复</div>
                           <div class="reply-error"  @click="replycancel(children, children.od)"><i class="el-icon-error"></i>取消发布</div>
                          </div>
                        </div>
                        </transition>                                                
                      </div>
                  </div>                     
                </div>                   
               </div>            
               <div class="hello">      
                <el-pagination  @size-change="pageSizeChange" @current-change="currentPageChange" :current-page="currentPage" :page-sizes="[ 5,10 ]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination> 
               </div>
            </div>
          </div>
        </div>
      </el-col>

    </el-row>
  </div>
</template>

<script>
import service from "../scripts/service.js";
export default {
  data: function() {
    return {
      od: 0,
      replyclick: false,
      delOneComments: "",
      addreplyList: "",
      addComments: "",
      totalRateValue: "",
      userCommentValue: "",
      checkWeeklyCollect: {},
      articleInfo: {},
      articleinfo: {},
      creatstaff: "",
      readNum: "",
      commentNum: "",
      creatstaff: {
        creatstaffname: "",
        creatstaffID: ""
      },
      commentsList: "",
      visitRoles: [],
      subjectName: "",
      //多选值
      multipleSelection: [],
      //当前页
      currentPage: 1,
      //分页大小
      pageSize: 10,
      //总记录数
      total: "",
      //多选值
      multipleSelection: [],
      //当前页
      userRateValue: "",
      currentPages: 1,
      //分页大小
      pageSizes: 10,
      //总记录数
      totals: ""
    };
  },
  mounted: function() {
    this.checkVisit();
    this.getCountVisit();
    this.getAauthor();
    this.getList();
    this.getComments();
    this.getCountAvg();
  },
  methods: {
    checkVisit() {
      let params = {
        pages: this.currentPages,
        pageSize: this.pageSizes,
        weeklyId: this.$route.query.id
      };
      service
        .checkVisit(params)
        .then(res => {
          console.log(res);
          this.visitRoles = res.data.list;
          this.totals = res.data.total;
          console.log(this.visitRoles);
        })
        .catch(err => {
          console.log(err);
        });
    },
    getCountVisit() {
      let params = {
        weeklyId: this.$route.query.id
      };
      service
        .getCountVisit(params)
        .then(res => {
          this.readNum = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    addVisitOne() {
      let params = {
        staffId: this.staffID,
        weeklyId: parseInt(this.$route.query.id)
      };
      service
        .addVisitOne(params)
        .then(res => {
          console.log(res);
          this.checkVisit();
          this.getCountVisit();
        })
        .catch(err => {
          console.log(err);
        });
    },
    getComments() {
      let params = {
        pages: this.currentPage,
        pageSize: this.pageSize,
        weeklyId: this.$route.query.id
      };
      service
        .getFyComments(params)
        .then(res => {
          this.total = res.data.total;
          this.commentNum = res.data.total;
          console.log(res);
          this.commentsList = res.data.list;
          console.log(this.commentsList);
        })
        .catch(err => {
          console.log(err);
        });
    },
    getAauthor() {
      service
        .getListSxByGType()
        .then(res => {
          console.log(res);
          this.Aauthor = res.data[4];
          console.log(this.Aauthor);
          this.staffname = this.Aauthor.staffname;
          this.staffID = this.Aauthor.staffId;
          this.addVisitOne();
        })
        .catch(err => {
          console.log(err);
        });
    },
    getList() {
      let params = {
        weeklyId: this.$route.query.id
      };
      console.log(params);
      service
        .getWeeklyDetailInfo(params)
        .then(res => {
          console.log(res);
          // 将获取回来的数据赋值给list
          this.articleinfo = res.data;
          this.creatstaff = res.data.staff;
          console.log(this.creatstaff);
          (this.creatstaffname = this.creatstaff.staffname),
            (this.articleInfo = {
              staffname: this.creatstaffname,
              subjectName: this.articleinfo.subjectName,
              keyWord: this.articleinfo.keyWord,
              createDate: this.articleinfo.createDate,
              staffType: this.articleinfo.staffType,
              subjectClass: this.articleinfo.subjectClass,
              subjectGroupType: this.articleinfo.subjectGroupType
            });
          // console.log(this.articleInfo);
        })
        .catch(err => {
          console.log(err);
        });
    },
    getCountAvg() {
      let params = {
        weeklyId: this.$route.query.id
      };
      service
        .getCountAvg(params)
        .then(res => {
          console.log(res);
          this.avgs = res.data.responseBody;
          this.totalRateValue = this.avgs.avg;
          console.log(this.totalRateValue);
        })
        .catch(err => {
          console.log(err);
        });
    },
    //删除评论
    delOneComment(item) {
      this.$confirm("确认删除？")
        .then(_ => {
          let params = {
            commentId: item.commentId,
            weeklyId: parseInt(this.$route.query.id)
          };
          console.log(params);
          service
            .delOneComment(params)
            .then(res => {
              console.log(res);
              this.getComments();
              this.getCountAvg();
            })
            .catch(err => {
              console.log(err);
            });
        })
        .catch(_ => {});
    },
    //添加评论
    addComment() {
      this.addComments = {
        comment: this.userCommentValue,
        commentGrade: this.userRateValue * 20,
        commentStaffId: this.staffID,
        weeklyId: this.$route.query.id
      };
      console.log(this.addComments);

      let params = {
        cwlc: this.addComments
      };
      service
        .addTopComments(params)
        .then(res => {
          console.log(res);
          this.getComments();
          this.getCountAvg();
        })
        .catch(err => {
          console.log(err);
        });
    },
    //添加回复
    addreplypa(item) {
      this.addreplyList = {
        comment: this.userreplyValue,
        commentGrade: "",
        commentStaffId: this.staffID,
        weeklyId: this.$route.query.id,
        commentParentId: item.commentId
      };
      let params = {
        cwlc: this.addreplyList
      };
      console.log(params);
      service
        .addBackComments(params)
        .then(res => {
          console.log(res);
          this.getComments();
          this.getCountAvg();
        })
        .catch(err => {
          console.log(err);
        });
    },
    //添加楼中楼回复
    addreplycd(children) {
      this.addreplyList = {
        comment: this.userreplyValue,
        commentGrade: "",
        commentStaffId: this.staffID,
        weeklyId: this.$route.query.id,
        commentParentId: children.commentId
      };
      let params = {
        cwlc: this.addreplyList
      };
      console.log(params);

      service
        .addBackComments(params)
        .then(res => {
          console.log(res);
          this.getComments();
          this.getCountAvg();
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 显示回复
    togglecomment(item) {
      item.od = 1;
    },
    // 显示楼中楼回复
    togglereply(children, od) {
      children.od = 1;
    },
    //取消回复
    commentcancel(item) {
      item.od = 0;
    },
    //取消楼中楼回复
    replycancel(children, od) {
      children.od = 0;
    },
    //评论
    //分页大小修改事件
    pageSizeChange: function(val) {
      console.log("每页 " + val + " 条");
      this.pageSize = val;
      this.getComments();
    },
    //当前页修改事件
    currentPageChange: function(val) {
      this.currentPage = val;
      console.log("当前页: " + val);
      this.getComments();
    },
    //访问
    //分页大小修改事件
    pageSizeChanges: function(val) {
      console.log("每页 " + val + " 条");
      this.pageSizes = val;
      this.checkVisit();
      this.getCountVisit();
    },
    //当前页修改事件
    currentPageChanges: function(val) {
      this.currentPages = val;
      console.log("当前页: " + val);
      this.checkVisit();
      this.getCountVisit();
    },
    //收藏
    collect() {
      this.$prompt("请输入收藏备注", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      })
        .then(({ value }) => {
          this.checkWeeklyCollect = {
            weeklyId: parseInt(this.$route.query.id),
            collectStaffId: parseInt(this.staffID),
            collectRemark: value
          };
          let params = {
            cwc: this.checkWeeklyCollect
          };
          console.log(params);
          service
            .addOneCollect(params)
            .then(response => {
              // this.$message({
              //   message: "收藏成功！",
              //   type: "success"
              // });
            })
            .catch(error => {
              console.log(error);
              // this.$message({
              //   message: "收藏失败，不能重复收藏",
              //   type: "info"
              // });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "取消收藏"
          });
        });
    },
    //推荐
    recommend() {
      let acc = {
        weeklyId: parseInt(this.$route.query.id),
        staffId: parseInt(this.staffID)
      };
      console.log(acc);

      this.$confirm("确定要推荐吗?", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        center: true
      })
        .then(() => {
          let params = {
            weeklyId: parseInt(this.$route.query.id),
            staffId: parseInt(this.$route.query.staffID)
          };
          console.log(params);
          service
            .addGoodWeeklyList(params)
            .then(response => {
              // this.$message({
              //   message: "推荐成功！",
              //   type: "success"
              // });
            })
            .catch(error => {
              console.log(error);
              // this.$message({
              //   message: "推荐失败",
              //   type: "success"
              // });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "取消推荐"
          });
        });
    }
  }
};
</script>

<style scoped>
